.App {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

h1 {
  color: #333;
}

label {
  display: block;
  margin-bottom: 10px;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  box-sizing: border-box;
  
}

button {
  background-color: #4CAF50;
  border-radius: 5rem;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

button a {
  color: white;
  text-decoration: none;
}

button:hover {
  background-color: #45a049;
}

div {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

img {
  max-width: 100%;
  height: auto;
  margin-bottom: 15px;
}

p {
  margin-bottom: 10px;
}

span {
  font-weight: bold;
  color: #555;
}